<template>
  <div class="container">
    <h1>Setting General</h1><hr />
    <div><span class="bi bi-plus-lg btn btn-primary" @click="newField"></span></div>
    <SettingField v-for="field in fields"
                  :key="field._id"
                  :fkey="field._id"
                  :type="field.type ?? 'text'"
                  :value="field.Value"
                  :placeholder="field.placeholder"
                  :description="field.description"
                  :title="field.title ?? field._id"
                  mask="false"
                  @editField="editField"
                  @saveField="saveField"
                  @removeField="removeField" />
    <div class="modal fade" id="modalConfirm" tabindex="-1" aria-labelledby="confirmLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="confirmLabel">Delete confirm</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body">
            <p v-html="confirmModalData.body"></p>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">No</button>
            <button type="button" @click="confirmModalData.callbackYes(confirmModal)" class="btn btn-primary">Yes</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="modalMeta" tabindex="-1" aria-labelledby="metaLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="metaLabel">{{ metaModalData.title }}</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body">
            <div class="form-group mt-2">
              <label for="metaKey">Key</label>
              <input id="metaKey" class="form-control" type="text" v-model="metaModalData.item._id" :disabled="metaModalData.modify">
              <label for="metaP">Placeholder</label>
              <input id="metaP" class="form-control" type="text" placeholder="Input Placeholder" v-model="metaModalData.item.placeholder">
              <label for="metaD">Description</label>
              <input id="metaD" class="form-control" type="text" placeholder="Input Description" v-model="metaModalData.item.description">
              <label for="metaT">Title</label>
              <input id="metaT" class="form-control" type="text" placeholder="Input Title" v-model="metaModalData.item.title">
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
            <button type="button" @click="metaModalData.callback(this)" class="btn btn-primary">Save</button>
          </div>
        </div>
      </div>
    </div>
    <div class="position-fixed bottom-0 end-0 p-3" style="z-index: 11">
      <div id="liveToast" class="toast hide" role="alert" aria-live="assertive" aria-atomic="true">
        <div class="toast-body">
          <span class="bi bi-x-circle btn text-danger" data-bs-dismiss="toast" aria-label="Close"></span>
          <span v-html="toastContent"></span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import global from '@/global'
import { Modal, Toast } from 'bootstrap'
import SettingField from '@/components/SettingField.vue'
export default {
  name: 'SettingGeneral',
  components: {
    SettingField
  },
  data: function () {
    return {
      fields: [],
      confirmModalData: {},
      confirmModal: {},
      metaModalData: {
        item: {
          _id: '',
          placeholder: '',
          description: '',
          title: ''
        },
        title: 'Modify'
      },
      metaModal: {},
      toast: {},
      toastContent: ''
    }
  },
  mounted: async function () {
    this.fields = await global.dbexec('select $ from KeyValue')
    this.confirmModal = new Modal(document.getElementById('modalConfirm'), {})
    this.metaModal = new Modal(document.getElementById('modalMeta'), {})
    this.toast = new Toast(document.getElementById('liveToast'), {})
  },
  methods: {
    editField (field) {
      this.metaModalData = {
        modify: true,
        title: 'Modify meta data of field "' + field + '"',
        item: this.fields.find(function (f) { return f._id === field }),
        callback: function (instance) {
          instance.metaModal.hide()
          console.log(instance.metaModalData.item)
        }
      }
      this.metaModal.show()
    },
    saveField (field, value) {
      console.log('save', field, value)
      this.showToast('ahihi')
    },
    showToast (msg) {
      this.toastContent = msg
      this.toast.show()
    },
    removeField (field) {
      this.confirmModalData = {
        body: 'Are you sure to delete <code>' + field + '</code>',
        callbackYes: function (modal) {
          modal.hide()
          console.log('remove confirm yes', field)
        }
      }
      this.confirmModal.show()
    },
    newField () {
      this.metaModalData = {
        modify: false,
        title: 'Add new meta data',
        item: {
          _id: '',
          placeholder: '',
          description: '',
          title: ''
        },
        callback: function (instance) {
          instance.metaModal.hide()
          console.log(instance.metaModalData.item)
        }
      }
      this.metaModal.show()
    }
  }
}</script>
