<template>
  <div class="form-group mt-2">
    <label :for="`id-${fkey}`">{{ title }} <code>{{ fkey }}</code></label><i class="bi bi-pencil-square btn text-primary" @click="$emit('editField', fkey)"></i><i class="bi bi-trash-fill btn text-danger" @click="$emit('removeField', fkey)"></i><i class="bi bi-life-preserver btn text-info" @click="$emit('saveField', fkey, fvalue)"></i>
    <input :id="`id-${fkey}`" class="form-control" :type="type" :placeholder="placeholder" :name="`name-${fkey}`" v-model="fvalue">
    <sub v-if="description">{{ description }}</sub>
  </div>
</template>

<script>
export default {
  name: 'SettingField',
  emits: ['editField', 'removeField', 'saveField'],
  props: {
    fkey: String,
    type: String,
    value: String,
    title: String,
    description: String,
    placeholder: String,
    mask: String
  },
  data: function () {
    return {
      fvalue: this.value
    }
  }
}
</script>
